import star2Image from 'img/star-regular.png';
import shareImage from 'img/share-alt-solid.png';
import arrowImage from 'img/chevron-right-solid.png';
import questionImage from 'img/question-solid.png';
import trophyImage from 'img/trophy-solid.png';
import trashImage from 'img/trash-solid.png';
import twitterImage from 'img/twitter-brands.png';
import envelopeImage from 'img/envelope-solid.png';
import globeImage from 'img/globe.png';
import handImage from 'img/hand.png';
import pcImage from 'img/percentage-solid.png';
