import {FinishScreen, MenuScreen} from 'finishScreen.js';

const axios = require('axios');

// This is the entry point of your game.
const target_width = 1500;
const target_height = 800;
const SECONDS_PER_DAY = 24*3600;
const TIMEZONEOFFSET = new Date().getTimezoneOffset()*60;

let wh = window.innerWidth / window.innerHeight;
let width = (wh>1) ? target_width : target_height;
let height = (wh>1) ? target_height : target_width;

const config = {
  width: width,
  height: height,
  type: Phaser.AUTO,
  backgroundColor: '#ffffff',
  scene: { preload,
    create, update },
  scale: {mode: Phaser.Scale.FIT,
    autoCenter: Phaser.Scale.CENTER_BOTH,
  }
};

var dayID;
var gph;
var timerLabel;
var welcomeScreen;
var finishScreen;
var menuScreen;
var data;
var global_this;
var userState = {};
var twoFingerTouch = false;
var initialTFT = {}

var termsURL = window.location.href;
termsURL = termsURL + ( (termsURL.slice(-1)=='/') ? '' : '/'  ) + 'terms';

const game = new Phaser.Game(config);



function preload() {
  this.load.image('menuImage', menuImage);
  this.load.image('starImage', starImage);
  this.load.image('star2Image', star2Image);
  this.load.image('shareImage', shareImage);
  this.load.image('arrowImage', arrowImage);
  this.load.image('trophyImage', trophyImage);
  this.load.image('questionImage', questionImage);
  this.load.image('trashImage', trashImage);
  this.load.image('twitterImage', twitterImage);
  this.load.image('envelopeImage', envelopeImage);
  this.load.image('handImage', handImage);
  this.load.image('globeImage', globeImage);
  this.load.image('pcImage', pcImage);
  this.load.json('puzdata', puzJSON);
}

function create() {

  console.log(termsURL);

  dayID = Math.floor( (Date.now() / 1000  - TIMEZONEOFFSET) / SECONDS_PER_DAY );
  dayID = (dayID - 19000) % 300;
  console.log('DayID: ', dayID);
  console.log(TIMEZONEOFFSET);

  global_this = this;

  data = this.cache.json.get('puzdata');

  this.add
    .text(120, 40, 'LINKR', { font: "bold 60px Arial", fill: "#000" })
    .setOrigin(0.5, 0.5).setDepth(2);


  timerLabel = new TimerLabel(this,120,100 );
  timerLabel.setStars(0);

  this.add.line(20,10,0,0,200,0,0x000000, 0.5).setOrigin(0,0).setLineWidth(2,2).setDepth(2);
  this.add.line(20,70,0,0,200,0,0x000000, 0.5).setOrigin(0,0).setLineWidth(2,2).setDepth(2);

  const menuButton = new MenuButton(this, width, 0, 'menuImage' ,0.5, menuClick );
  const questionButton = new MenuButton(this, width-100, 7, 'questionImage' ,0.4, questionClick );
  const trophyButton = new MenuButton(this, width-5, 100, 'trophyImage' ,0.45, trophyClick );

  this.input.on('pointerdown', function (pointer, currentlyOver) {
      onMouseDown(pointer);
  }.bind(this) );

  this.input.on('pointermove', function (pointer, currentlyOver) {
      onMouseMove(pointer);
  }.bind(this) );

  this.input.on('pointerup', function (pointer, currentlyOver) {
      onMouseUp(pointer);
  }.bind(this) );

  this.input.addPointer(1);

 console.log("create ok");

 welcomeScreen = new WelcomeScreen(this, width/2, height/2-75, height, termsURL);
 finishScreen = new FinishScreen(this,width/2,height/2, continueCallback);
 menuScreen = new MenuScreen(this,width/2,height/2-100, setPalette, deleteAll);

 userState = loadFile();

 // Check if new user
 if (!("uuid" in userState)) {
   // New user
   userState = genNewUser();
   welcomeScreen.show();
 }

  resolveState(userState);

  saveFile();
 //finishScreen.show();

}

function loadFile() {
  let rv = JSON.parse(localStorage.getItem('saveFile'));
  if (rv==null) {
    rv = {};
  }

  return rv;

}

function saveFile() {
  localStorage.setItem('saveFile',JSON.stringify(userState));
}

function resolveState(s) {

  console.log(s);

    if (s.dayID!=dayID) {
      s.dayID = dayID;
      s.time1 = 0;
      s.time2 = 0;
      s.time3 = 0;
      s.net_avg_time1=0;
      s.net_avg_time2=0;
      s.net_avg_time3=0;
      s.completed = 0;
      s.puzID = 0;
      updateServer(s);
    }

    // Set to default palette
    if (!("palette" in s)) {
      s.palette = 0;
    }
    if (!("offset" in s)) {
      s.offset = new Date().getTimezoneOffset();
    }

    let puzData = data.Puzzles[s.puzID][dayID];
    let puzEndpoints = puzData.p;
    let puzConnections = data.Connections[ puzData.t ];
    let puzPositions = data.Positions[ puzData.t ];

    gph = new Graph(global_this, puzPositions, puzConnections, puzEndpoints , width, height, s.palette );

    timerLabel.setStars(userState.puzID + userState.completed);

    if (welcomeScreen.visible){
      return;}

    if (s.completed) {

      timerLabel.stopTimer();
      if (s.puzID==0)
        timerLabel.writeTimerLabel(s.time1);

      if (s.puzID==1)
        timerLabel.writeTimerLabel(s.time2);

      if (s.puzID==2)
        timerLabel.writeTimerLabel(s.time3);

      finishScreen.show(s,false);
      gph.isCompleted = true;

    } else {
      timerLabel.startTimer();
    }

    gph.show();

}

function genNewUser() {
  return {
    "dayID" : 0,
    "puzID" : 0,
    "completed" : 0,
    "time1" : 0,
    "time2" : 0,
    "time3" : 0,
    "total_completed1" : 0,
    "total_completed2" : 0,
    "total_completed3" : 0,
    "total_time1" : 0,
    "total_time2" : 0,
    "total_time3" : 0,
    "net_avg_time1" : 0,
    "net_avg_time2" : 0,
    "net_avg_time3" : 0,
    "uuid" : Phaser.Utils.String.UUID(),
    "palette" : 0,
    "offset" : new Date().getTimezoneOffset(),
  }
}

function continueCallback() {

  finishScreen.hide();
  closeAndAdvance();
  saveFile();

}

function setPalette(p) {
  userState.palette = p;
  saveFile();
  gph.setPalette(p);
  console.log("Palette set to ", p);

}

function closeAndAdvance() {

  if (userState.puzID>=2)
    return;

  userState.puzID++;
  userState.completed = 0;
  gph.hide(loadNewPuzzle);

}

function showFinishScreen() {

  finishScreen.show(userState,false);

}

function loadNewPuzzle() {

  gph.visible = false;
  gph.destroy();

  let puzData = data.Puzzles[userState.puzID][dayID];
  let puzEndpoints = puzData.p;
  let puzConnections = data.Connections[ puzData.t ];
  let puzPositions = data.Positions[ puzData.t ];

  gph = new Graph(global_this, puzPositions, puzConnections, puzEndpoints , width, height, userState.palette );

  gph.show();
  gph.visible = true;
  gph.alpha = 0;


  timerLabel.startTimer();

}


function update(t,dt) {

  let pauseFlag = (menuScreen.visible) || (finishScreen.visible) || (welcomeScreen.visible);

  timerLabel.updateTimerLabel(dt, pauseFlag);

  welcomeScreen.updateScreen();

  gph.updateScreen();

  finishScreen.updateScreen();
  menuScreen.updateScreen();

  if ( (pauseFlag==false) && (gph.animating==0) ) {

    if (this.input.pointer1.isDown && this.input.pointer2.isDown) {
      // Two finger touch

      if (twoFingerTouch==false) {
        twoFingerTouch = true;
        console.log('starting TFT');

        gph.mouseArc.hide();
        gph.nodeSelected = -1;

        let dx = this.input.pointer1.x - this.input.pointer2.x;
        let dy = this.input.pointer1.y - this.input.pointer2.y;

        initialTFT = {
          mean_x : (this.input.pointer1.x + this.input.pointer2.x)/2,
          mean_y : (this.input.pointer1.y + this.input.pointer2.y)/2,
          dist : Math.sqrt(dx*dx+dy*dy),
          gph_x : gph.vx,
          gph_y : gph.vy,
          gph_sf : gph.vsf,
          snap : false,

        }

      } else {

        var ddy = (this.input.pointer1.y + this.input.pointer2.y)/2 - initialTFT.mean_y;
        var ddx = (this.input.pointer1.x + this.input.pointer2.x)/2 - initialTFT.mean_x;

        var dsf = (this.input.pointer1.y - this.input.pointer2.y)*(this.input.pointer1.y - this.input.pointer2.y) +
            (this.input.pointer1.x - this.input.pointer2.x)*(this.input.pointer1.x - this.input.pointer2.x);
        dsf = Math.sqrt(dsf) / initialTFT.dist;

        if (initialTFT.snap==true) {
          gph.nsf = initialTFT.gph_sf * dsf;
        } else {
          if ( (dsf>1.1) || (dsf<0.9) ) {
            initialTFT.snap=true;
          }
        }


        gph.posChanged = true;

      }

    } else {
      twoFingerTouch = false;
    }

  } else {
    twoFingerTouch = false;
  }

}

function menuClick() {
  console.log('Menu Clicked');
  if (menuScreen.visible){
    menuScreen.hide();
    return;
  }
  console.log('Showing menu');
  menuScreen.show(userState);

}

function trophyClick() {
  console.log('Trophy Clicked');
  if (finishScreen.visible)
    return;
  if (welcomeScreen.visible)
    return;
  finishScreen.show(userState,true);

}

function questionClick() {
  console.log('question Clicked');
  if (finishScreen.visible)
    return;
  if (welcomeScreen.visible)
    return;
  welcomeScreen.show();

}

function onMouseDown(p) {
  console.log('onMouseDown');

  if (welcomeScreen.visible==true) {
    return;
  }
  if (menuScreen.visible) {
    menuScreen.mouseDown(p.x - width/2, p.y - height/2);
    return;
  }
  if (twoFingerTouch) {
    return;
  }

  gph.handleMouseDown(p);

  if (gph.nodeSelected>=0) {
    gph.mouseArc.updateStartpoint( gph.nodelist[ gph.nodeSelected ] );
    gph.mouseArc.show();
  }

  onMouseMove(p);
}

function onMouseMove(p) {
  if (p.isDown==false)
      return;
  if (twoFingerTouch) {
    return;
  }

  if (welcomeScreen.visible==true) {
    return;
  }
  if (finishScreen.visible) {
    return;
  }
  if (menuScreen.visible) {
    return;
  }

  if (gph.nodeSelected<0)
    return;


  if (gph.checkMouseOver(p)>=0) {
    if (gph.nodeSelected>=0)
      gph.mouseArc.updateStartpoint( gph.nodelist[ gph.nodeSelected ] );
    else {
        gph.mouseArc.hide();
        gph.completed();
    }
  }

  gph.mouseArc.updateEndpoint( p.x, p.y );

  if (gph.isCompleted &&  timerLabel.timerStarted ) {
    completePuzzle();
  }

  //console.log('onMouseMove');

}

function completePuzzle() {

  timerLabel.stopTimer();
  timerLabel.setStars(userState.puzID+1);
  userState.completed = 1;

  let tt = Math.floor( timerLabel.deltaTime );

  if (userState.puzID==0) {
    userState.time1 = tt;
    userState.total_time1 += tt;
    userState.total_completed1 += 1;
  }

  if (userState.puzID==1) {
    userState.time2 = tt;
    userState.total_time2 += tt;
    userState.total_completed2 += 1;
  }

  if (userState.puzID==2) {
    userState.time3 = tt;
    userState.total_time3 += tt;
    userState.total_completed3 += 1;
  }

    console.log('Completed');
    updateServer( userState );

    gph.flash( showFinishScreen );
    saveFile();





}

function deleteAll() {
  console.log('Deleted');
  userState = {};
  saveFile();

  location.reload();

}

function onMouseUp(p) {
  if (welcomeScreen.visible) {
    welcomeScreen.hide();
    gph.show();
    timerLabel.startTimer();
    return;
  }
  if (menuScreen.visible) {
    return;
  }
  gph.handleMouseUp(p);
  gph.mouseArc.hide();

  finishScreen.mouseDown(p.x - width/2, p.y - height/2);

}

function getURL() {

  return 'https://5673wz6h6c.execute-api.eu-west-2.amazonaws.com/linkr';

}
function updateServer(s) {

  let ss = {
    uuid : s.uuid,
    dayID : s.dayID,
    time1 : s.time1,
    time2 : s.time2,
    time3 : s.time3,
    total_time1 : s.total_time1,
    total_time2 : s.total_time2,
    total_time3 : s.total_time3,
    total_completed1 : s.total_completed1,
    total_completed2 : s.total_completed2,
    total_completed3 : s.total_completed3,
    offset : s.offset,
  };

axios.post(getURL(), ss)
  .then(res => {
    console.log(`statusCode: ${res.status}`);
    console.log(res);
    if ("data" in res ) {
      var data_dayid = res["data"].dayid;
        console.log(res["data"], data_dayid);
      if (data_dayid!=userState.dayID) {
        return
      }
      var mt1 = ( userState.time1<=0 ) ? 0 : res["data"].t1;
      var mt2 = ( userState.time2<=0 ) ? 0 : res["data"].t2;
      var mt3 = ( userState.time3<=0 ) ? 0 : res["data"].t3;
      console.log("upd net times", mt1,mt2,mt3);

      userState.net_avg_time1 = mt1;
      userState.net_avg_time2 = mt2;
      userState.net_avg_time3 = mt3;

      finishScreen.updateNetTimes( mt1,mt2,mt3 );
      saveFile();
    }

  })
  .catch(error => {
    console.error(error);
  })

}
