import 'phaser';

export class TimerLabel extends Phaser.GameObjects.Container {

  constructor(scene,x,y) {
    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.depth = 2;

    this.startTime = this.scene.time.now;
    this.deltaTime = 0;
    this.timerStarted = false;

    this.label = this.scene.add
      .text(0, 0, '00:00:00', { font: "bold 40px Overpass Mono", fill: "#000" })
      .setOrigin(0.5, 0.5).setAlpha(0.7);

    this.add(this.label);

    this.scene.add.existing(this);

    this.star1 = this.scene.add.image(-45, 45, 'star2Image').setOrigin(0.5, 0.5);
    this.star1.setScale(0.4);
    this.star1.setTintFill(0xFFD700);
    this.star1.setAlpha(0.5);

    this.star2 = this.scene.add.image(0, 45, 'star2Image').setOrigin(0.5, 0.5);
    this.star2.setScale(0.4);
    this.star2.setTintFill(0xFFD700);
    this.star2.setAlpha(0.5);

    this.star3 = this.scene.add.image(45, 45, 'star2Image').setOrigin(0.5, 0.5);
    this.star3.setScale(0.4);
    this.star3.setTintFill(0xFFD700);
    this.star3.setAlpha(0.5);

    this.add(this.star1);
    this.add(this.star2);
    this.add(this.star3);


  }

  setStars(x) {

    if (x==0) {

      this.star1.setTexture('star2Image');
      this.star2.setTexture('star2Image');
      this.star3.setTexture('star2Image');
      this.star1.setAlpha(0.75);
      this.star2.setAlpha(0.25);
      this.star3.setAlpha(0.25);

      return;
    }
    if (x==1) {

      this.star1.setTexture('starImage');
      this.star2.setTexture('star2Image');
      this.star3.setTexture('star2Image');
      this.star1.setAlpha(1);
      this.star2.setAlpha(0.75);
      this.star3.setAlpha(0.25);

      return;
    }
    if (x==2) {

      this.star1.setTexture('starImage');
      this.star2.setTexture('starImage');
      this.star3.setTexture('star2Image');
      this.star1.setAlpha(1);
      this.star2.setAlpha(1);
      this.star3.setAlpha(0.75);

      return;
    }

      this.star1.setTexture('starImage');
      this.star2.setTexture('starImage');
      this.star3.setTexture('starImage');
      this.star1.setAlpha(1);
      this.star2.setAlpha(1);
      this.star3.setAlpha(1);


  }

  startTimer() {
    if (this.timerStarted)
      return;

    this.timerStarted = true;
    this.startTime = this.scene.time.now;
    this.deltaTime = 0;
    this.label.text = "00:00:00";
    this.label.alpha = 0.5;

  }

  stopTimer() {
    this.timerStarted = false;
    this.label.alpha = 1;
  }

  addLeadingZero(s) {
    if (s>9)
      return s;

      return "0" + s;

  }
  updateTimerLabel(dt, pauseFlag) {

    if (this.timerStarted==false)
      return;
    if (dt<0) {
      dt=0;
    }
    if (dt>1000) {
      dt=1000;
    }

    if (pauseFlag==false)
      this.deltaTime += dt/1000;

    this.writeTimerLabel(this.deltaTime);

  }
  writeTimerLabel(dt) {

  let hh = Math.floor(dt/3600);
  let mm = Math.floor(dt/60) % 60 ;
  let ss = Math.floor(dt) % 60 ;

  this.label.text = this.addLeadingZero(hh) + ":" +
                    this.addLeadingZero(mm) + ":" + this.addLeadingZero(ss);
  }

}


export class MenuButton extends Phaser.GameObjects.Container {

  constructor(scene, x, y, img, scale, clickFunction) {

    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.depth = 100
    this.button = this.scene.add.image(0, 0, img).setInteractive()
        .setOrigin(1.25, -0.15);

    this.button.setScale(scale);
    this.button.alpha = 0.35;

    this.selected = false;

    this.add(this.button);

    this.button.on('pointerdown', function () {
        this.button.alpha = 1;
        console.log("pointerdown");
        this.selected = true;
    }.bind(this));

    this.button.on('pointerup', function () {
        this.button.alpha = 0.35;
        console.log("pointerup");
        if (this.selected) {
          clickFunction();
        }
        this.selected = false;

    }.bind(this));

    this.button.on('pointerout', function () {
        this.button.alpha = 0.35;
        console.log("pointerout");
        this.selected = false;
    }.bind(this));

    this.scene.add.existing(this);
  }

}
