    this.add(this.bg2);

   this.bg4 = this.scene.add.graphics();
   this.bg4.fillStyle(0xfbfaf2,1);
   this.bg4.fillRoundedRect(-350,320,700,130,30);

   this.add(this.bg4);


    this.titleText = this.scene.add.text(0, -220,
      'LINKR version ' + version,
       { font: "bold 26px Verdana", fill: "#555555" })
         .setOrigin(0.5, 0.5);
    this.add( this.titleText );

