import 'phaser';

const SHADOWWIDTH = 10;
const TIMELENGTH = 350;

export class WelcomeScreen extends Phaser.GameObjects.Container {

  constructor(scene,x,y,maxheight,termsURL) {
    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.default_y = y;
    this.depth = 1000;


    this.add( this.scene.add.rectangle(0, 0, 5000, 5000, 0xffffff, 0.75) );

    this.bg = this.scene.add.graphics();
    this.bg.fillStyle(0x000000,0.35);
    this.bg.fillRoundedRect(-300-SHADOWWIDTH,
              -300-SHADOWWIDTH,
              600+2*SHADOWWIDTH,600+2*SHADOWWIDTH,30);

    this.bg2 = this.scene.add.graphics();
    this.bg2.fillStyle(0xfbfaf2,1);
    this.bg2.fillRoundedRect(-300,-300,600,600,30);

    this.add(this.bg);
    this.add(this.bg2);

    this.add( this.scene.add.text(270, -280, 'x', { font: "36px Arial", fill: "#666666" })
         .setOrigin(0.5, 0.5) );

    // this.add( this.scene.add.text(0, -245,
    //    'Solve three puzzles as fast as you can.  ',
    //     { font: "28px Verdana", fill: "#000" })
    //       .setOrigin(0.5, 0.5) );

     this.add( this.scene.add.text(0, -240,
        'Tap and drag to connect the numbers,',
         { font: "28px Verdana", fill: "#000" })
           .setOrigin(0.5, 0.5) );

      this.add( this.scene.add.text(0, -200,
         'link them all to solve the puzzle!',
          { font: "28px Verdana", fill: "#000" })
            .setOrigin(0.5, 0.5) );

    this.add(
    this.scene.add.line(0,240,-270,0,270,0,0x888888, 1).setOrigin(0,0).setLineWidth(1,1)
        );

    this.add( this.scene.add.text(0, 270,
      'Three new LINKR puzzles every day!',
       { font: "bold 28px Verdana", fill: "#000" })
         .setOrigin(0.5, 0.5) );


    this.add( this.scene.add.text(0, 160,
    'Puzzles only have one solution,',
    { font: "28px Verdana", fill: "#000" })
     .setOrigin(0.5, 0.5) );
   this.add( this.scene.add.text(0, 200,
   'and solutions always use every space.',
   { font: "28px Verdana", fill: "#000" })
    .setOrigin(0.5, 0.5) );

    this.bg3 = this.scene.add.graphics();
    this.bg3.fillStyle(0x111111,0.85);
    this.bg3.fillRoundedRect(-350,maxheight/2-50,700,70,35)

    this.add( this.bg3 );

    this.add( this.scene.add.text(0, maxheight/2-50+20,
        'View the usage terms \& conditions and privacy notice at',
         { font: "20px Verdana", fill: "#ffffff" })
           .setOrigin(0.5, 0.5) );
    this.add( this.scene.add.text(0, maxheight/2-50+50,
       termsURL,
        { font: "20px Verdana", fill: "#00ffff" })
          .setOrigin(0.5, 0.5) );

    this.tutorialScreen = new TutorialScreen(scene,50,-20);
    this.add( this.tutorialScreen );

    this.scene.add.existing(this);

    this.alpha = 0;
    this.visible = false;
    this.animating = 0;
    this.start_anim = 0;

  }

  show() {
    if (this.visible==true)
      return;
    if (this.animating>0)
      return;

    this.visible = true;
    this.start_anim = this.scene.time.now;
    this.animating = 1;

  }

  hide() {
    if (this.visible==false)
      return;
    if (this.animating>0)
      return;

    this.visible = true;
    this.start_anim = this.scene.time.now;
    this.animating = 2;

  }

  updateScreen() {

    if (this.visible==false)
      return;

    this.tutorialScreen.updateAnim();

    if (this.animating==0) {
      return;
    }

    let dt = (this.scene.time.now - this.start_anim)/TIMELENGTH;
    if (dt<0)
      dt = 0;

    if (this.animating==1) {
      if (dt>1) {
        this.animating=0;
        this.alpha = 1;
        return;
      }
      this.alpha = dt;
      this.y = this.default_y + (1-dt)*35;
      return;
    }

    if (this.animating==2) {
      if (dt>1) {
        this.animating=0;
        this.alpha = 0;
        this.visible = false;
        return;
      }
      this.alpha = 1-dt;
      return;
    }


  }

}

const MININODERAD = 20;
const dx = 100;

export class TutorialScreen extends Phaser.GameObjects.Container {

  constructor(scene,x,y) {
    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;

    this.add(this.scene.add.line(-dx,-dx,0,0,dx,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(0,-dx,0,0,dx,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(-dx,0,0,0,dx,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(0,0,0,0,dx,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(-dx,dx,0,0,dx,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(0,dx,0,0,dx,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(-2*dx,0,0,0,dx,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));

    this.add(this.scene.add.line(-dx,-dx,0,dx,0,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(-dx,0,0,dx,0,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(dx,-dx,0,dx,0,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(dx,0,0,dx,0,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(0,-dx,0,dx,0,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));
    this.add(this.scene.add.line(0,0,0,dx,0,0,0x000, 1).setOrigin(0,0).setLineWidth(3,3));


    this.add( this.scene.add.circle(-dx,-dx,MININODERAD,0x000000) );
    this.add( this.scene.add.circle(0,-dx,MININODERAD,0x000000) );
    this.add( this.scene.add.circle(dx,-dx,MININODERAD,0x000000) );

    this.add( this.scene.add.circle(-dx,0,MININODERAD,0x000000) );
    this.add( this.scene.add.circle(dx,0,MININODERAD,0x000000) );

    this.add( this.scene.add.circle(0,dx,MININODERAD,0x000000) );
    this.add( this.scene.add.circle(dx,dx,MININODERAD,0x000000) );


    this.arc1 = this.scene.add.line(0,0,-2*dx,0,-2*dx,0,0xff0000)
        .setOrigin(0,0);
    this.arc1.isStroked = true;
    this.arc1.setLineWidth(MININODERAD,MININODERAD);
    this.add(this.arc1);

    this.circle1 = this.scene.add.circle(-2*dx,0,MININODERAD,0xff0000);
    this.add( this.circle1 );


    this.arc2 = this.scene.add.line(0,0,-dx,-dx,-dx,-dx,0x0000ff)
        .setOrigin(0,0);
    this.arc2.isStroked = true;
    this.arc2.setLineWidth(MININODERAD,MININODERAD);
    this.add(this.arc2);

    this.circle2 = this.scene.add.circle(-dx,-dx,MININODERAD,0x0000ff);
    this.add( this.circle2 );


    this.arc3 = this.scene.add.line(0,0,dx,-dx,dx,-dx,0x0000ff)
        .setOrigin(0,0);
    this.arc3.isStroked = true;
    this.arc3.setLineWidth(MININODERAD,MININODERAD);
    this.add(this.arc3);

    this.circle3 = this.scene.add.circle(dx,-dx,MININODERAD,0x0000ff);
    this.add( this.circle3 );


    this.arc4 = this.scene.add.line(0,0,dx,dx,dx,dx,0x0000ff)
        .setOrigin(0,0);
    this.arc4.isStroked = true;
    this.arc4.setLineWidth(MININODERAD,MININODERAD);
    this.add(this.arc4);

    this.circle4 = this.scene.add.circle(dx,dx,MININODERAD,0x0000ff);
    this.add( this.circle4 );


    let sf = 1.5
    // Red 1a
    this.add( this.scene.add.circle(-2*dx, 0,MININODERAD,0xff0000) );
    this.add( this.scene.add.circle(-2*dx, 0,MININODERAD/sf,0x000000) );
    this.add( this.scene.add.text(-2*dx, 0, '1', { font: "26px Arial", fill: "#fff" })
            .setOrigin(0.5, 0.5).setStroke(0x000000,5) );

    // Red 1b
    this.add( this.scene.add.circle(0, 0,MININODERAD,0xff0000) );
    this.add( this.scene.add.circle(0, 0,MININODERAD/sf,0x000000) );
    this.add( this.scene.add.text(0, 0, '1', { font: "26px Arial", fill: "#fff" })
            .setOrigin(0.5, 0.5).setStroke(0x000000,5) );


    // Blue 2a
    this.add( this.scene.add.circle(-dx,dx,MININODERAD,0x0000ff) );
    this.add( this.scene.add.circle(-dx,dx,MININODERAD/sf,0x000000) );
    this.add( this.scene.add.text(-dx, dx, '2', { font: "26px Arial", fill: "#fff" })
            .setOrigin(0.5, 0.5).setStroke(0x000000,5) );

    // Blue 2b
    this.add( this.scene.add.circle(-dx,-dx,MININODERAD,0x0000ff) );
    this.add( this.scene.add.circle(-dx,-dx,MININODERAD/sf,0x000000) );
    this.add( this.scene.add.text(-dx, -dx, '2', { font: "26px Arial", fill: "#fff" })
            .setOrigin(0.5, 0.5).setStroke(0x000000,5) );



    this.hand = this.scene.add.image(0, 0, 'handImage').setOrigin(0.32,0.08).setScale(0.75);
    this.hand.visible = false;

    this.add( this.hand );

    this.scene.add.existing(this);

  }

  updateAnim() {

    let dt = this.scene.time.now/1000;
    let dt2 = 0;
    let HANDDIST = 35;

    dt = dt % 10;
    dt = dt - 0.5;

    if (dt<0) {
      return;
    }

    this.arc1.setTo(-2*dx,0,-2*dx ,0);
    this.circle1.x = -2*dx ;
    this.circle2.visible=false;
    this.arc2.visible = false;
    this.circle3.visible=false;
    this.arc3.visible = false;
    this.circle4.visible=false;
    this.arc4.visible = false;
    this.hand.visible = false;
    this.hand.setAlpha(1);

    if (dt<0.5) {
      dt2 = dt/0.5;

      this.hand.visible = true;
      this.hand.setAlpha( dt2 );
      this.hand.y = HANDDIST * (1-dt2);
      this.hand.x = -2*dx;

      return;
    }

    dt -= 0.5;

    if (dt<1) {
      dt2 = dt/1;
      this.arc1.setTo(-2*dx,0,-2*dx*(1-dt2),0);
      this.circle1.x = -2*dx*(1-dt2);
      this.hand.visible = true;
      this.hand.y = 0;
      this.hand.x = -2*dx*(1-dt2);
      return;
    }

    dt -= 1;

    if (dt<0.5) {
      dt2 = dt/0.5;
      this.arc1.setTo(-2*dx,0,0,0);
      this.circle1.x = 0;
      this.hand.visible = true;
      this.hand.y = HANDDIST*dt2;
      this.hand.setAlpha(1-dt2);
      this.hand.x = 0;
      return;
    }

    dt -= 0.5;



    if (dt<0.5) {
      dt2 = dt/0.5;

      this.arc1.setTo(-2*dx,0,0,0);
      this.circle1.x = 0;

      this.hand.visible = true;
      this.hand.setAlpha( dt2 );
      this.hand.y = -dx + HANDDIST * (1-dt2);
      this.hand.x = -dx;

      return;
    }

    dt -= 0.5;

    if (dt<1) {
      dt2 = dt/1;
      this.arc1.setTo(-2*dx,0,0,0);
      this.circle1.x = 0;

      this.circle2.visible=true;
      this.arc2.visible = true;
      this.circle2.x = 2*dx*(dt2)-dx;
      this.arc2.setTo(-dx,-dx,2*dx*(dt2)-dx,-dx);
      this.hand.visible = true;
      this.hand.y = -dx;
      this.hand.x = this.circle2.x;
      return;
    }

    dt -=1;


    if (dt<1) {
      dt2 = dt/1;
      this.arc1.setTo(-2*dx,0,0,0);
      this.circle1.x = 0;

      this.circle2.visible=true;
      this.arc2.visible = true;
      this.circle2.x = 2*dx-dx;
      this.arc2.setTo(-dx,-dx,2*dx-dx,-dx);

      this.circle3.visible=true;
      this.arc3.visible = true;
      this.circle3.y = 2*dx*(dt2)-dx;
      this.arc3.setTo(dx,-dx,dx,2*dx*(dt2)-dx);
      this.hand.visible = true;
      this.hand.y = this.circle3.y;
      this.hand.x = dx;
      return;
    }

    dt -=1;


    if (dt<1) {
      dt2 = dt/1;
      this.arc1.setTo(-2*dx,0,0,0);
      this.circle1.x = 0;

      this.circle2.visible=true;
      this.arc2.visible = true;
      this.circle2.x = 2*dx-dx;
      this.arc2.setTo(-dx,-dx,2*dx-dx,-dx);

      this.circle3.visible=true;
      this.arc3.visible = true;
      this.circle3.y = 2*dx-dx;
      this.arc3.setTo(dx,-dx,dx,2*dx-dx);

      this.circle4.visible=true;
      this.arc4.visible = true;
      this.circle4.x = -2*dx*(dt2)+dx;
      this.arc4.setTo(dx,dx,-2*dx*(dt2)+dx,dx);
      this.hand.visible = true;
      this.hand.x = this.circle4.x;
      this.hand.y = dx;
      return;
    }

    dt -= 1;

    if (dt<0.5) {
      dt2 = dt/0.5;
      this.arc1.setTo(-2*dx,0,0,0);
      this.circle1.x = 0;

      this.circle2.visible=true;
      this.arc2.visible = true;
      this.circle2.x = 2*dx-dx;
      this.arc2.setTo(-dx,-dx,2*dx-dx,-dx);

      this.circle3.visible=true;
      this.arc3.visible = true;
      this.circle3.y = 2*dx-dx;
      this.arc3.setTo(dx,-dx,dx,2*dx-dx);

      this.circle4.visible=true;
      this.arc4.visible = true;
      this.circle4.x = -2*dx+dx;
      this.arc4.setTo(dx,dx,-2*dx+dx,dx);

      this.hand.visible = true;
      this.hand.y = dx+HANDDIST*dt2;
      this.hand.setAlpha(1-dt2);
      this.hand.x = -dx;
      return;
    }
    dt -=0.5;

    dt -= 1;


    if (dt<0.5) {
      dt2 = dt/0.5;
      this.arc1.setTo(-2*dx,0,0,0);
      this.circle1.x = 0;

      this.circle2.visible=true;
      this.arc2.visible = true;
      this.circle2.x = 2*dx-dx;
      this.arc2.setTo(-dx,-dx,2*dx-dx,-dx);

      this.circle3.visible=true;
      this.arc3.visible = true;
      this.circle3.y = 2*dx-dx;
      this.arc3.setTo(dx,-dx,dx,2*dx-dx);

      this.circle4.visible=true;
      this.arc4.visible = true;
      this.circle4.x = -2*dx+dx;
      this.arc4.setTo(dx,dx,-2*dx+dx,dx);

      this.hand.visible = true;
      this.hand.y = HANDDIST*(1-dt2);
      this.hand.setAlpha(dt2);
      this.hand.x = -2*dx;
      return;
    }
    dt -= 0.5;
    if (dt<0.5) {
      dt2 = dt/0.5;

      this.circle2.visible=true;
      this.arc2.visible = true;
      this.circle2.x = 2*dx-dx;
      this.arc2.setTo(-dx,-dx,2*dx-dx,-dx);

      this.circle3.visible=true;
      this.arc3.visible = true;
      this.circle3.y = 2*dx-dx;
      this.arc3.setTo(dx,-dx,dx,2*dx-dx);

      this.circle4.visible=true;
      this.arc4.visible = true;
      this.circle4.x = -2*dx+dx;
      this.arc4.setTo(dx,dx,-2*dx+dx,dx);

      this.hand.visible = true;
      this.hand.y = HANDDIST*(dt2);
      this.hand.setAlpha((1-dt2));
      this.hand.x = -2*dx;
      return;
    }
    dt -= 0.5;
    dt -= 0.35;

    if (dt<0.5) {
      dt2 = dt/0.5;

      this.circle2.visible=true;
      this.arc2.visible = true;
      this.circle2.x = 2*dx-dx;
      this.arc2.setTo(-dx,-dx,2*dx-dx,-dx);

      this.circle3.visible=true;
      this.arc3.visible = true;
      this.circle3.y = 2*dx-dx;
      this.arc3.setTo(dx,-dx,dx,2*dx-dx);

      this.circle4.visible=true;
      this.arc4.visible = true;
      this.circle4.x = -2*dx+dx;
      this.arc4.setTo(dx,dx,-2*dx+dx,dx);

      this.hand.visible = true;
      this.hand.y = HANDDIST*(1-dt2)-dx;
      this.hand.setAlpha(dt2);
      this.hand.x = -dx;
      return;
    }
    dt -= 0.5;
    if (dt<0.5) {
      dt2 = dt/0.5;
      this.hand.visible = true;
      this.hand.y = HANDDIST*(dt2)-dx;
      this.hand.setAlpha((1-dt2));
      this.hand.x = -dx;
      return;
    }




    return;

    //
    // this.circle3.visible=(dt3>0);
    // this.arc3.visible=(dt3>0);
    //
    // this.arc3.setTo(dx,-dx,dx,2*dx*(dt3)-dx);
    // this.circle3.y = 2*dx*(dt3)-dx;
    //
    // let dt4 = (dt<4) ? dt-3 : 1;
    // dt4 = (dt4<0) ? 0 : dt4;
    //
    // this.circle4.visible=(dt4>0);
    // this.arc4.visible=(dt4>0);
    //
    // this.arc4.setTo(dx,dx,-2*dx*(dt4)+dx,dx);
    // this.circle4.x = -2*dx*(dt4)+dx;


  }

}
