import 'phaser';


import {Node, Arc, MouseArc, BoldArc} from 'node.js';

const GAMESIZE = 360;
const TIMELENGTH = 350;

function rgb(r,g,b) {
  return Phaser.Display.Color.GetColor(255*r,255*g,255*b);
}

export var colorArray = [
  rgb(1,1,1),
  rgb(1, 0, 0), // red
  rgb(0, 0, 1), // blue
  rgb(0, 1, 0), // green
  rgb(0, 1, 1),// aqua
  rgb(0.54, 0.17, 0.89), // blue violet
  rgb(1, 0.8 , 0),// yellow
  rgb(0.1, 0.4, 0.1), // dark green
  rgb(1, 0.4, 0.7),// pink
  rgb(0.6, 0.32, 0.18), // sienna/brown
  rgb(1, 1, 0.75), //cream
  rgb(.82, .71, .55), // tan
  rgb(.44, 0.5, .56),// slate grey
  rgb(1, 0.46, 0.1), // burnt orange
  rgb(0.35 , 0, 0.0), // dark maroon
  rgb(1, 1, 0.5),
  rgb(.5, .5, 0.5)
]


export var alt_colorArray = [
  rgb(1,1,1),
  0xcc6677, // red
  0x332288, // blue
  0x117733, // green
  0x44aa99,// aqua
  0x999933, // dark green
  0xaa4499, // blue violet
  0xddcc77,// yellow
  0x882255,// pink
  0x88ccee, // sienna/brown
  0xaaaaaa, //cream
  0xee7733, // tan
  0xbbccee,
  0xffcccc,
  0x997700,// slate grey
  rgb(1, 1, 0.5),
  rgb(.5, .5, 0.5)
]


export class Graph extends Phaser.GameObjects.Container {

  constructor(scene, PP, CC, EE, width, height, paletteFlag ) {

    super(scene);
    this.scene = scene;
    this.x = 0;
    this.y = 0;
    this.ww = width
    this.hh = height
    this.PP = PP;
    this.CC = CC;
    this.EE = EE;

    this.vx = 0;
    this.vy = 0;
    this.nx = 0;
    this.ny = 0;
    this.vsf = 1;
    this.nsf = 1;

    this.posChanged = false;

    this.portraitFlag = (height > width);
    this.paletteFlag = paletteFlag;

    this.nodeSelected = -1;
    this.isCompleted = false;

    this.nodelist = [];
    this.all_arcs = [];
    this.all_bold_arcs = [];
    this.arclist = {};

    this.is_endpoint = [];
    this.twin_endpoint = [];
    this.occupancy = [];
    this.arcOccupancy = {};

    this.mouseArc = new MouseArc( this.scene );

    let k=0;

    // NODES
    k=0;
    for (const n of PP ) {

      let x = (width>height) ? n[0] : n[1];
      let y = (width>height) ? n[1] : -n[0];

      let tmp = new Node(scene, width/2 + GAMESIZE * x,  height/2 + GAMESIZE * y, k);

      this.nodelist.push( tmp );

      this.occupancy.push( 0 );
      this.is_endpoint.push( false );
      this.twin_endpoint.push( -1 );

      //this.add(tmp);

      k++;

    }

    k = 1;
    for (const e of EE) {

      let e1 = e[0];
      let e2 = e[1];

      this.nodelist[e1].setLabel(k);
      this.nodelist[e2].setLabel(k);

      this.nodelist[e1].setOuterColor( this.getPalette(k) );
      this.nodelist[e2].setOuterColor( this.getPalette(k) );

      this.occupancy[e1] = k;
      this.occupancy[e2] = k;
      this.is_endpoint[e1] = true;
      this.is_endpoint[e2] = true;
      this.twin_endpoint[e1] = e2;
      this.twin_endpoint[e2] = e1;

      k++;

    }

    k = 0;
    for (const a of CC) {

      this.arclist[k] = {};
      this.arcOccupancy[k] = {};

      for (const aa of a) {
        if (aa<k) {
          continue;
        }

        let a1 = new Arc(scene, this.nodelist[k] , this.nodelist[aa]);
        let a2 = new BoldArc(scene, this.nodelist[k] , this.nodelist[aa]);

        this.arclist[k][aa] = a2;
        this.arcOccupancy[k][aa] = 0;

        this.all_arcs.push(a1);
        this.all_bold_arcs.push(a2);

        this.add(a1);
      }

      k++;
    }

    k = 0;
    for (const a of CC) {
      for (const aa of a) {
        if (aa<k) {
          continue;
        }
      this.add( this.arclist[k][aa] );
    }
    k++;
    }

    this.add(this.mouseArc);

    for (let ii=0;ii<this.nodelist.length;ii++) {
      this.add( this.nodelist[ii] );
    }


    this.scene.add.existing(this);
    this.alpha = 0;
    this.visible = false;
    this.animating = 0;
    this.start_anim = 0;

  };

  setPalette(p) {
    this.paletteFlag = p;
    this.refreshPalette();

  }

  refreshPalette() {

    for (const n of this.nodelist) {
      this.updateNodeColor( n.nodenum );

      for (const c of this.CC[ n.nodenum]) {

        if (c<n.nodenum)
          continue;

          let v = this.get_arcOccupancy(c,n.nodenum);

          this.get_arclist(c,n.nodenum).setBoldColor( (v==0) ? -1 : this.getPalette(v) );

      }
    }



  }

  getPalette(k) {

    if (this.paletteFlag==1) {
      return alt_colorArray[k];
    }

    return colorArray[k];

  }

  show() {
    if (this.visible==true)
      return;
    if (this.animating>0)
      return;

    this.visible = true;
    this.start_anim = this.scene.time.now;
    this.animating = 1;

  }

  hide(callback) {
    if (this.visible==false)
      return;
    if (this.animating>0)
      return;

    this.visible = true;
    this.callback = callback;
    this.start_anim = this.scene.time.now;
    this.original_vsf = this.vsf;
    this.animating = 2;

  }

  updatePositions() {

    if (this.posChanged==false)
      return;
    this.posChanged = false;

    let xx = this.vx;
    let yy = this.vy;
    let sf = this.vsf;

    for (let ii=0; ii<this.nodelist.length; ii++) {

      this.nodelist[ii].x = this.ww/2 + (this.nodelist[ii].original_x - this.ww/2 )*sf + xx;
      this.nodelist[ii].y = this.hh/2 + (this.nodelist[ii].original_y - this.hh/2 )*sf + yy;

    }

    for (let ii=0; ii<this.all_arcs.length; ii++) {
      this.all_arcs[ii].updateArc();
      this.all_bold_arcs[ii].updateArc();
    }

  }

  updateScreen() {

    if (this.visible==false)
      return;

    if (this.nsf>1.25) {
      this.nsf = 1.25;
    }
    if (this.nsf<0.75) {
      this.nsf = 0.75;
    }

    if (this.nx<-100){
      this.nx=-100;
    }
    if (this.nx>100) {
      this.nx=100;
    }

    if (this.ny<-100){
      this.ny=-100;
    }
    if (this.ny>100) {
      this.ny=100;
    }

    if (this.portraitFlag==false) {
      this.ny = 0;
    } else {
      this.nx = 0;
    }

    this.vy = this.ny;
    this.vx = this.nx;
    this.vsf = this.nsf;

    this.updatePositions();

    if (this.animating==0) {
      return;
    }

    let dt = (this.scene.time.now - this.start_anim)/TIMELENGTH;
    if (dt<0)
      dt = 0;

    if (this.animating==1) {
      dt-= 0.5;
      if (dt<0){
        this.alpha = 0;
        return;}
      if (dt>1) {
        this.animating=0;
        this.alpha = 1;
        this.nsf = 1;
        this.posChanged = true;
        this.updatePositions();
        return;
      }
      this.alpha = dt;
      this.nsf = 0.9 + 0.1*dt;
      this.posChanged = true;
      this.updatePositions();
      return;
    }

    if (this.animating==2) {
      if (dt>1) {
        this.animating=0;
        this.alpha = 0;
        this.visible = false;
        this.callback();
        return;
      }
      this.alpha = 1-dt;
      this.nsf = this.original_vsf + 0.1*dt;
      this.posChanged = true;
      this.updatePositions();
      return;
    }

    if (this.animating==3) {
      if (dt>0.6) {
        this.animating=0;
        this.alpha = 1;
        this.visible = true;
        this.callback();
        return;
      }
      this.alpha = 0.3+0.7*(dt/0.6);
      return;
    }

    if (this.animating==4) {
      if (dt>0.6) {
        this.animating=0;
        this.starburst.destroy();
        return;
      }
      this.starburst.updateLines(dt/0.6);
      return;
    }


  }

  completed() {

    if (this.isCompleted)
      return;

    for (const ep of this.EE) {

      if (this.linked(ep[0],ep[1])) {
        continue;
      } else {
        this.isCompleted = false;
        return false;
      }

    }

    this.isCompleted = true;
    return true;

  }

  linked(n1,n2) {

    let n = n1;
    let prev = n1;

    while (n!=n2) {

      let foundit = false;

      for (const nn of this.CC[n]) {

        if (nn==prev) {
          continue;
        }

        if (this.get_arcOccupancy(n,nn) ) {
          prev = n;
          n=nn;
          foundit = true;
          break;
        }

      }

      if (foundit==false) {
        return false;
      }

    }

    return true;

  }


  flash(callback) {
      if (this.animating==4) {
        this.animating=0;
        this.starburst.destroy();
      }
      if (this.animating>0)
        return;

      this.visible = true;
      this.start_anim = this.scene.time.now;
      this.animating = 3;
      this.callback = callback;

  }

  handleMouseDown(p) {


  if (this.visible==false)
    return;

    console.log('Handing mousedown');
    this.nodeSelected = -1;

    if (this.isCompleted)
      return;

    for (const n of this.nodelist) {
      if (this.occupancy[ n.nodenum ]==0) {
        continue;
      }
      if (n.checkInside(p.x,p.y)) {
        this.nodeSelected = n.nodenum;
        break;
      }
    }

  }

  checkMouseOver(p) {
    if (this.nodeSelected<0)
      return -1;

    for (const n of this.nodelist) {

      if (n.checkInside(p.x,p.y)) {

        if (n.nodenum==this.nodeSelected)
          continue;

        let cc = 0;
        let ep = 0;
        if (this.trialExtend(n) ) {
          cc = this.occupancy[n.nodenum];
          console.log(cc);
          ep = this.findEndpoint( this.EE[cc-1][0] , this.EE[cc-1][0]  )
          {
            if (ep==this.twin_endpoint[ this.EE[cc-1][0] ]) {
              // Color completed!
              if (this.animating==0) {
                this.animating=4;
                this.start_anim = this.scene.time.now;
                this.starburst = new Starburst(this.scene,n.x,n.y, this.getPalette(cc) );
              }
              this.nodeSelected = -1;
            }
          }

          return n.nodenum;
        }

        if ((this.occupancy[ n.nodenum]==0) || ( (this.occupancy[ n.nodenum]==this.occupancy[this.nodeSelected])
                &&
              (this.is_endpoint[n.nodenum]) ) ) {
          // Find cxn closer
          let dx = (n.x - this.nodelist[this.nodeSelected].x);
          let dy = (n.y - this.nodelist[this.nodeSelected].y);
          let dd = dx*dx + dy*dy;
          let mind = this.nodeSelected;

          for (const c of this.CC[this.nodeSelected]) {
            if (this.occupancy[c]!=0)
              continue;
            dx = (n.x - this.nodelist[c].x);
            dy = (n.y - this.nodelist[c].y);
            if ((dx*dx+dy*dy)<dd) {
              dd = (dx*dx+dy*dy);
              mind = c;
            }
          }
          console.log(this.nodeSelected, mind, dd);

          if (mind!=this.nodeSelected) {
            if (this.trialExtend( this.nodelist[mind] ) ) {
              return n.nodenum;
            }
          }


        }

      }
    }


  }

  get_arclist(n1,n2) {
  let min_n = Math.min(n1 , n2);
  let max_n = Math.max(n1 , n2);
  return this.arclist[min_n][max_n];
  }
  get_arcOccupancy(n1,n2) {
  let min_n = Math.min(n1 , n2);
  let max_n = Math.max(n1 , n2);
  return this.arcOccupancy[min_n][max_n];
  }
  set_arcOccupancy(n1,n2,v) {
  let min_n = Math.min(n1 , n2);
  let max_n = Math.max(n1 , n2);
  this.arcOccupancy[min_n][max_n] = v;
  this.get_arclist(n1,n2).setBoldColor( (v==0) ? -1 : this.getPalette(v) );

  this.updateNodeColor(n2);
  this.updateNodeColor(n1);

  }
  set_occupancy(n,v) {
    this.occupancy[n] = v;
    this.updateNodeColor(n);
    this.completed();
  }

  updateNodeColor(n) {

    let v = this.occupancy[n];
    let cc = this.getPalette(v);

    if (v==0) {
      this.nodelist[n].setColor(0x000000);
    } else {
      // Count how many cxns
      let cxns = this.countConnections(n);

      if (cxns==0) {
        this.nodelist[n].setOuterColor(cc);
      }
      if (cxns==2) {
        this.nodelist[n].setColor(cc);
      }
      if (cxns==1) {
        if (this.is_endpoint[n]) {
          this.nodelist[n].setColor(cc);
        } else {
          this.nodelist[n].setOuterColor(cc);
        }
      }
    }

  }

  countConnections(n) {
    let cxns = 0;

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(c,n)>0)
        cxns++;
    }
    return cxns;
  }

  trialExtend(n) {

    if (this.areConnected(n.nodenum , this.nodeSelected)==false) {
      return false;
    }

    let curnum = this.occupancy[ this.nodeSelected ];
    let curcol = this.getPalette( curnum );

    // Empty node
    if (this.occupancy[n.nodenum]==0) {

      this.cullMeUpTo(this.nodeSelected);

      this.set_occupancy(n.nodenum,curnum);

      this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);

      this.nodeSelected = n.nodenum;

      return true;
    }

    // Same color
    if (this.occupancy[n.nodenum]==curnum) {

      if (this.get_arcOccupancy(this.nodeSelected, n.nodenum )>0 ) {

        if ( (this.countConnections(this.nodeSelected)==1) &&
            (this.is_endpoint[this.nodeSelected]==false) ) {
              this.set_occupancy(this.nodeSelected , 0);
              this.set_arcOccupancy(this.nodeSelected, n.nodenum,0);
              this.nodeSelected = n.nodenum;
          return true;
        }
        return false;
      }

      this.cullMeUpTo(this.nodeSelected);
      if (this.occupancy[n.nodenum]>0)
        this.cullMeUpTo(n.nodenum);

      if ((this.occupancy[n.nodenum]==curnum) && (this.occupancy[this.nodeSelected]==curnum)) {
        this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);
        this.nodeSelected = n.nodenum;
      } else {

        if (this.occupancy[n.nodenum]==curnum) {
          this.nodeSelected = n.nodenum;
          return true;
        }

        if (this.occupancy[this.nodeSelected]==curnum) {

          this.set_occupancy(n.nodenum, curnum);
          this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);
          this.nodeSelected = n.nodenum;
          return true;
        }

      }

      return true;
    }

    // Different color
    // no if statement needed...

    if (this.is_endpoint[n.nodenum])
      return false;

    this.cullMeUpTo(this.nodeSelected);

    this.splitSnake(n.nodenum);

    this.set_occupancy(n.nodenum,curnum);

    this.set_arcOccupancy(this.nodeSelected, n.nodenum, curnum);

    this.nodeSelected = n.nodenum;

    return true;
  }

  splitSnake(n) {

    this.cullMeUpTo(n);

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(n,c)>0) {
        this.set_arcOccupancy(n,c,0);
      }
    }
  }

  cullMeUpTo(n) {

    let feps = [];

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(n,c)>0) {
        feps.push( this.findEndpoint(n,c) );
      }
    }

    console.log(feps);

    if (feps.length==0)
      return;

    if (feps.length==1) {
      if (this.is_endpoint[n]==false)
        return;
      if (this.is_endpoint[feps[0]]) {
        this.delRandomLink(n);
        return;
      }
      this.delSnake(feps[0],n);
      return;
    }

    let n1 = feps[0];
    let n2 = feps[1];

    let ep1 = this.is_endpoint[n1];
    let ep2 = this.is_endpoint[n2];

    if (ep1 && !ep2) {
        // delete from n2 to n
        this.delSnake(n2,n);
      return;
    }
    if (!ep1 && ep2) {
        // delete from n1 to n
        this.delSnake(n1,n);
      return;
    }
    if (ep1 && ep2) {
        this.delRandomLink(n);
      return;
    }

  }

  delRandomLink(n) {


  for (const c of this.CC[n]) {
    if (this.get_arcOccupancy(n,c)<=0)
      continue;
      this.set_arcOccupancy(n,c,0);

   break;
  }



  }

  delSnake(st,en) {

    let n = st;
    let bflag = false;

    while (n!=en) {
      this.set_occupancy(n,0);
      bflag = false;
      for (const c of this.CC[n]) {
        if (this.get_arcOccupancy(n,c)<=0)
          continue;

        this.set_arcOccupancy(n,c,0);
        n=c;
        bflag = true;
        break;
      }
      if (bflag==false) {
        console.log('Error');
        break;
      }
    }

  }

  findEndpoint(n1,n2) {

    for (const c of this.CC[n2]) {
      if (c==n1) {
        continue;
      }
      if (this.get_arcOccupancy(n2,c)<=0)
        continue;

        return this.findEndpoint(n2,c);
    }

    return n2;

  }

  areConnected(n1,n2) {

    if (this.CC[n2].includes(n1) ) {
      return true;
    }
    return false;

  }

  handleMouseUp(p) {

    if (this.nodeSelected<0)
      return;
    if (this.visible==false)
      return;

    if (this.is_endpoint[this.nodeSelected]) {

      if (this.nodelist[this.nodeSelected].checkInside(p.x,p.y)) {
        // Clear endpoint onwards
        console.log('clear snake');
        this.clearSnake( this.nodeSelected );

      }

    }

    this.nodeSelected = -1;

  }

  clearSnake(n) {

    if (this.is_endpoint[n]==false) {
      this.set_occupancy(n,0);
    }

    for (const c of this.CC[n]) {
      if (this.get_arcOccupancy(n,c)>0) {

        this.set_arcOccupancy(n,c,0);
        this.clearSnake(c);

      }

    }


  }

}




export class Starburst extends Phaser.GameObjects.Container {

  constructor(scene,x,y , cc) {

    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.cc = cc;
    this.W = 5;

    this.arclist = []

    for (let ii=0;ii<8;ii++) {

      let aa = this.scene.add.line(0,0,0,0,0,0,cc)
          .setOrigin(0,0);
      aa.isStroked = true;
      aa.setLineWidth(this.W,this.W);
      this.add(aa);
      this.arclist.push(aa);
    }

    this.scene.add.existing(this);

  }

  updateLines(dt) {

    if (dt<0)
      dt = 0;
    if (dt>1)
      dt = 1;

    let w1 = 35 + dt*75;
    let w2 = 75 + dt*35;

    for (let ii=0;ii<8;ii++) {

      let ang = ii*3.1415/4;
      let xx = Math.sin(ang);
      let yy = Math.cos(ang);

      this.arclist[ii].setTo(w1*xx,w1*yy,w2*xx,w2*yy);

    }


  }

}
