import 'phaser';

const SHADOWWIDTH = 10;
const TIMELENGTH = 350;


export class ColorButton extends Phaser.GameObjects.Container {

  constructor(scene,x,y, h,w,c, txt, img, callback ) {
    super(scene);
    this.scene = scene;
    this.x = x;
    this.y = y;
    this.w = w;

    this.ss = this.scene.add.graphics();
    this.ss.fillStyle(0x000000,1);
    this.ss.fillRoundedRect(5-w/2,5-h/2,w,h,10);
    this.ss.setInteractive();
    this.add( this.ss );

    this.fg = this.scene.add.graphics();
    this.fg.fillStyle(c,1);
    this.fg.fillRoundedRect(-w/2,-h/2,w,h,10);

    this.add( this.fg )

    this.txt1 = this.scene.add.text(3, 3, txt,
       { font: "bold 36px Verdana", fill: "#000" })
         .setOrigin(0.5, 0.5);

    this.add(
      this.txt1
    );

    this.txt2 = this.scene.add.text(0, 0, txt,
       { font: "bold 36px Verdana", fill: "#fff" })
         .setOrigin(0.5, 0.5);

    this.add(
      this.txt2
    );

    this.img1 = this.scene.add.image(w/2+3, 3, img).setOrigin(2, 0.5).setScale(0.4).setTintFill(0x000);

    this.add(
      this.img1
    );

    this.img2 = this.scene.add.image(w/2, 0, img).setOrigin(2, 0.5).setScale(0.4);
    this.add(
      this.img2
    );


    this.scene.add.existing(this);
  }


  updateBounce() {

    if (this.visible==false)
      return;

    let BounceY = -10;

    let dt = this.scene.time.now/1000;
    dt = Math.sin(dt*3.14);
    dt = 1-Math.abs(dt);

    this.txt1.x = 3-BounceY * dt;
    this.txt2.x = 0-BounceY*dt;

    this.img1.x = this.w/2-BounceY*dt+3;
    this.img2.x = this.w/2-BounceY*dt;


  }


}
